<template>
  <div class="history">
    <p class="title">历史数据查询</p>
    <div class="chose">
      <p>
        <span>数据类型</span>
        <a-dropdown>
          <a class="ant-dropdown-link" @click="(e) => e.preventDefault()">
            游客数据 <a-icon type="down" />
          </a>
          <a-menu slot="overlay">
            <a-menu-item>
              <a href="javascript:;">游客数据</a>
            </a-menu-item>
            <a-menu-item>
              <a href="javascript:;">销售数据</a>
            </a-menu-item>
          </a-menu>
        </a-dropdown>
      </p>

      <p>
        <span>数据类型</span>
        <a-dropdown>
          <a class="ant-dropdown-link" @click="(e) => e.preventDefault()">
            按日 <a-icon type="down" />
          </a>
          <a-menu slot="overlay">
            <a-menu-item>
              <a href="javascript:;">按日</a>
            </a-menu-item>
            <a-menu-item>
              <a href="javascript:;">按月</a>
            </a-menu-item>
            <a-menu-item>
              <a href="javascript:;">按年</a>
            </a-menu-item>
          </a-menu>
        </a-dropdown>
      </p>

      <p>
        <span>时间范围</span>
        <a-range-picker
          :placeholder="['Start month', 'End month']"
          format="YYYY-MM"
          :value="value"
          :mode="mode2"
          @panelChange="handlePanelChange2"
          @change="handleChange"
        />
      </p>

      <p>
        <span>景区</span>
  <a-cascader :options="options" placeholder="Please select" @change="onChange" />
      </p>
              <a-button type="primary">
      查看大数据
    </a-button>
    </div>
    <!-- <echart :option="option" /> -->
  </div>
</template>
<script>
// import Echart from "@/components/Echart.vue";

export default {
  mounted() {
    this.$unlock();
  },
  data() {
    return {
      mode1: "time",
      mode2: ["month", "month"],
      value: [],
            options: [
        {
          value: 'zhejiang',
          label: 'Zhejiang',
          children: [
            {
              value: 'hangzhou',
              label: 'Hangzhou',
              children: [
                {
                  value: 'xihu',
                  label: 'West Lake',
                },
              ],
            },
          ],
        },
        {
          value: 'jiangsu',
          label: 'Jiangsu',
          children: [
            {
              value: 'nanjing',
              label: 'Nanjing',
              children: [
                {
                  value: 'zhonghuamen',
                  label: 'Zhong Hua Men',
                },
              ],
            },
          ],
        },
      ],
    };
  },
  components: {
    // Echart,
  },
  methods: {
    onChange(date, dateString) {
      console.log(date, dateString);
    },
    handleOpenChange1(open) {
      if (open) {
        this.mode1 = "time";
      }
    },
    handleChange(value) {
      this.value = value;
    },
    handlePanelChange1(value, mode) {
      this.mode1 = mode;
    },
    handlePanelChange2(value, mode) {
      this.value = value;
      this.mode2 = [
        mode[0] === "date" ? "month" : mode[0],
        mode[1] === "date" ? "month" : mode[1],
      ];
    },
  },
};
</script>
<style lang="less" scoped>
.history {
  color: #00FFFF;
  a{
  color: #00FFFF;
  }
  .title:before {
    content: "";
    width: 400px;
    height: 1px;
    background: linear-gradient(
      135deg,
      #0ed6f5 10%,
      rgba(14, 214, 245, 0.1) 100%
    );
    position: absolute;
    top: 3%;
    left: 55%;
  }
  .title:after {
    content: "";
    width: 400px;
    height: 1px;
    background: linear-gradient(
      135deg,
      rgba(14, 214, 245, 0.1) 10%,
      #0ed6f5 100%
    );
    position: absolute;
    top: 3%;
    right: 55%;
  }
  .chose {
    display: flex;
    justify-content: space-around;
    align-items: center;
  }
}
</style>
<style lang="less">
.ant-input {
  background-color: unset;
  border: 1px solid #00ffff;
  color: #00ffff;
  border-radius: 20px;
  width: 60%;
  height: 25px;
}
.ant-calendar-range-picker-separator {
  color: unset;
}
.ant-calendar-range .ant-calendar-input,
.ant-calendar-range .ant-calendar-time-picker-input {
  background-color:paleturquoise;
}
.ant-calendar {
  background-color:paleturquoise;
}
.ant-calendar-month-panel {
  background:paleturquoise;
}
.ant-calendar-month-panel-cell-disabled .ant-calendar-month-panel-month,
.ant-calendar-month-panel-cell-disabled .ant-calendar-month-panel-month:hover {
  background:paleturquoise;
}
.ant-calendar-header a:hover {
  color: #ccc;
}
.ant-calendar-month-panel-month:hover {
  background: yellow;
}
.ant-calendar-month-panel-selected-cell .ant-calendar-month-panel-month:hover {
  background: green;
}
.ant-calendar-month-panel-selected-cell .ant-calendar-month-panel-month {
  background: orange;
}
